import { useEffect, useState } from "react";
import { Button, Card, Col, Row } from "react-bootstrap";
import uploadImg from "../../../images/upload-img.png";
import "./style.scss";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import BaseService from "../../../services/BaseService";
import Loader from "../../common/Loader";
import { Translate } from "../../Enums/Tranlate";
import BannerService from "../../../services/BannerService";

const Banners = () => {
	const [isAdd, setIsAdd] = useState(true);
	const [loading, setLoading] = useState(false);
	const [submitLoading, setSumbitLoading] = useState(false);
	const Auth = useSelector((state) => state.auth?.auth);
	const lang = useSelector((state) => state.auth?.lang);
	const isExist = (data) => Auth?.admin?.admin_roles?.includes(data);
	const bannerService = new BannerService();

	const [formData, setFormData] = useState([
		{ src: "", loading: false },
		{ src: "", loading: false },
		{ src: "", loading: false },
		{ src: "", loading: false },
		{ src: "", loading: false },
	]);

	useEffect(() => {
		bannerService?.getList()?.then((res) => {
			if (res && res?.status === 200) {
				if (res.data?.data?.length === 0) {
					return;
				}
				if (res.data?.data?.length > 0) {
					setIsAdd(false);
				}
				let data = res.data?.data?.map((item) => {
					return {
						src: item?.image,
						loading: false,
					};
				});

				if (data?.length < 5) {
					let complete = [];
					for (let i = data?.length; i < 5; i++) {
						complete.push({ src: "", product: "" });
					}
					setFormData([...data, ...complete]);
				} else {
					setFormData([...data]);
				}
			}
			setLoading(false);
		});
	}, []);

	const fileHandler = (e, index) => {
		if (e?.target.files?.length === 0) {
			return;
		}
		setFormData((prev) =>
			prev?.map((item, ind) =>
				ind === index ? { ...item, loading: true } : item
			)
		);
		let filesAll = e.target.files;
		const filesData = Object.values(filesAll);

		new BaseService()
			.postUpload(filesData[0])
			.then((res) => {
				if (res && res?.data?.status) {
					setFormData((prev) =>
						prev?.map((item, ind) =>
							ind === index
								? { ...item, src: res.data.url, loading: false }
								: item
						)
					);
				} else {
					setFormData((prev) =>
						prev?.map((item, ind) =>
							ind === index ? { ...item, loading: false } : item
						)
					);
				}
			})
			.catch((e) =>
				setFormData((prev) =>
					prev?.map((item, ind) =>
						ind === index ? { ...item, loading: false } : item
					)
				)
			);
	};

	const onSubmit = () => {
		let data = {
			banners: formData
				?.filter((res) => !!res.src)
				?.map((item) => {
					let res = {
						image: item?.src,
					};
					return res;
				}),
		};

		if (isAdd) {
			if (data.banners?.length === 0) {
				toast.error("Add Image");
				return;
			}
			setSumbitLoading(true);
			bannerService
				.create(data)
				?.then((res) => {
					if (res && res?.status === 201) {
						toast.success("Banners Added Successfully");
						setIsAdd(false);
					}
					setSumbitLoading(false);
				})
				.catch(() => setSumbitLoading(false));
		} else {
			setSumbitLoading(true);
			bannerService
				.update(data)
				?.then((res) => {
					if (res && res?.status === 200) {
						toast.success("Banners Updated Successfully");
						setIsAdd(false);
					}
					setSumbitLoading(false);
				})
				.catch(() => setSumbitLoading(false));
		}
	};

	if (loading) {
		return (
			<Card className="py-5">
				<Card.Body>
					<Loader />
				</Card.Body>
			</Card>
		);
	}
	return (
		<>
			{formData?.map((data, index) => {
				return (
					<Card className="p-4" key={index}>
						<Row>
							<Col md={12}>
								<h4>
									{Translate[lang].banner} {index + 1}
								</h4>
								<div className="image-placeholder">
									{!data?.src && (
										<div className="avatar-edit">
											<input
												type="file"
												onChange={(e) => {
													if (!isExist("banners")) {
														toast.error("Not Allowed, Don`t have Permission");
														return;
													}
													fileHandler(e, index);
												}}
												id={`imageUpload${index}`}
											/>
											<label htmlFor={`imageUpload${index}`} name=""></label>
										</div>
									)}
									<button
										className="delete"
										type="button"
										onClick={() => {
											let update = formData?.map((item, ind) => {
												if (ind === index) {
													return {
														...item,
														src: "",
													};
												} else {
													return item;
												}
											});
											setFormData([...update]);
										}}>
										<i className="la la-trash text-danger"></i>
									</button>
									<div className="avatar-preview">
										<div>
											{!!data?.src &&
												!data.loading &&
												(data?.src?.includes(".mp4") ||
												data?.src?.includes(".webm") ||
												data?.src?.includes(".ogg") ? (
													<video className="w-100 h-100" controls>
														<source src={data?.src} type="video/mp4" />
														<source src={data?.src} type="video/ogg" />
														<source src={data?.src} type="video/webm" />
													</video>
												) : (
													<img src={data?.src} alt="icon" />
												))}
											{!data?.src && !data.loading && (
												<img
													id={`saveImageFile${index}`}
													src={uploadImg}
													alt="icon"
													style={{
														width: "80px",
														height: "80px",
													}}
												/>
											)}
											{data.loading && <Loader />}
										</div>
									</div>
								</div>
							</Col>
						</Row>
					</Card>
				);
			})}
			{isExist("banners") && (
				<div className="d-flex justify-content-between mb-4">
					<Button
						variant="outline-primary"
						className="px-5"
						disabled={submitLoading}
						type="button"
						onClick={() => setFormData((prev) => [...prev, { image: "" }])}>
						{Translate[lang].add_new_value}
					</Button>
					<Button
						variant="primary"
						className="px-5"
						disabled={submitLoading}
						onClick={() => onSubmit()}>
						{Translate[lang].submit}
					</Button>
				</div>
			)}
		</>
	);
};
export default Banners;
